@import './reset.scss';
@import './typography.scss';
@import './buttons.scss';

.countdown {
	// background-color: #63bfac;
	color: #38312c;
	height: 100vh;
	overflow: hidden;
	display: grid;
	grid-template-rows: 1fr 4fr 1fr;
	align-items: center;
	a {
		justify-self: flex-start;
		button {
			width: 100%;
		}
	}
	.countdown__timer {
		display: flex;
		flex-direction: row;
		align-items: center;
		width: 70%;
		margin-left: 15%;
		> img {
			height: 50vh;
			width: auto;
			margin-right: 5%;
		}
	}

	footer {
		display: flex;
		justify-content: space-between;
		> p {
			padding-left: 2rem;
			text-align: left;
		}
	}
}
